* {
  font-family: "Abel", sans-serif;
}

body {
  background-image: linear-gradient(
    to right,
    #1d4350,
    #a43931
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-attachment: fixed;
}
a {
  font-family: "Abel", sans-serif;
  font-size: small;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.5);
}

a:hover {
  text-decoration: none;
  color: #000;
}

.appContainer {
  padding-top: 50px;
}

.appRow {
  height: 93vh;
  padding-bottom: 10px;
}

.app_main {
  background-color: whitesmoke;
  height: 100%;

  padding: 25px 15px;
  box-shadow: 0 0 10px skyblue;
  border-radius: 20px;
}

@media (max-width: 990px) {
  .app_side {
    height: auto;
  }

  .app_main {
    margin: 30px 0;
    height: auto;
  }
}

@media (min-width: 991px) {
  .app_side {
    padding-right: 10px;
    padding-left: 10px;
    height: auto;
    margin-bottom: 20px;
  }

  .app_main {
    height: auto;
    margin-bottom: 20px;
  }
}
